import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import { Product, ProductFormData } from '../types/inventory';
import toast from 'react-hot-toast';

interface InventoryState {
  products: Product[];
  usageRecords: {
    id: string;
    date: string;
    totalWaterAmount: number;
    selectedProducts: { id: string; calculatedUsage: number }[];
  }[];
  addProduct: (product: ProductFormData) => void;
  updateProduct: (id: string, product: ProductFormData) => void;
  deleteProduct: (id: string) => void;
  recordUsage: (data: { totalWaterAmount: number; selectedProducts: { id: string; calculatedUsage: number }[] }) => void;
  setFullState: (state: InventoryState) => void;
}

export const useInventoryStore = create<InventoryState>()(
  persist(
    (set) => ({
      products: [],
      usageRecords: [],

      addProduct: (productData) => {
        const newProduct = {
          ...productData,
          id: crypto.randomUUID(),
          lastUpdated: new Date().toISOString()
        };

        set((state) => ({
          products: [...state.products, newProduct]
        }));
        
        toast.success('rn baaryla eklendi');
      },

      updateProduct: (id, productData) => {
        set((state) => ({
          products: state.products.map(product => 
            product.id === id 
              ? { ...productData, id, lastUpdated: new Date().toISOString() }
              : product
          )
        }));
        
        toast.success('rn baaryla gncellendi');
      },

      deleteProduct: (id) => {
        set((state) => ({
          products: state.products.filter(product => product.id !== id)
        }));
        
        toast.success('rn baaryla silindi');
      },

      recordUsage: (data) => {
        const usageRecord = {
          id: crypto.randomUUID(),
          date: new Date().toISOString(),
          ...data
        };

        set((state) => ({
          usageRecords: [...state.usageRecords, usageRecord],
          products: state.products.map(product => {
            const usage = data.selectedProducts.find(p => p.id === product.id);
            if (usage) {
              return {
                ...product,
                quantity: product.quantity - usage.calculatedUsage,
                lastUpdated: new Date().toISOString()
              };
            }
            return product;
          })
        }));

        toast.success('Kullanm kayd eklendi');
      },

      setFullState: (newState) => {
        set(newState);
      }
    }),
    {
      name: 'inventory-storage',
      version: 1
    }
  )
);